          },
        ],
        showtable:['产品名称','版本号','上传用户','上传时间','检测出漏洞个数','检测完成'],
        properties:['id','productName','version','user','time','numberOfVulnerabilities','status'],
        colors:[ '#7400bf', '#7f36ba', '#8c52b5', '#976fad', '#cc94b5', '#c97b96', '#df8cab', '#ff7bac']
      }
    },
    methods: {
      lookover: function(){
          this.$router.push("/console/detail")
      }
    },
    created() {

  axios.get('http://120.26.95.9:8081/submission/reportList').then((res1) => {
    let res = res1.data[0];
    console.log(res);
    this.applys[0].id = res.Report_ID;
    this.applys[0].productName = res.product;
    this.applys[0].version = res.version;
    this.applys[0].user = res.user;
    this.applys[0].time = res.uploadtime;
    this.applys[0].numberOfVulnerabilities = res.vul_count;
    this.applys[0].status = res.state;
    console.log(this.applys);
})
},
  }
</script>
